/**
 * 注意事项：1.请求订单数据要把自己请求的数据加到info数组 2.其他事项看注释
 * */
import uniSegmentedControl from "@/components/Order/uni-segmented-control/uni-segmented-control.vue";
import uniPopup from "@/components/Order/uni-popup/uni-popup.vue";
import uniIcons from "@/components/Order/uni-icons/uni-icons.vue";
import uniPopup2 from "@/components/Order/uni-popup/uni-popup2.vue";
import emptyData from "@/components/CTKY/emptyData/emptyData.vue"; //无数据时显示内容
import $taxi from '../../common/Czc.js';
import $privateTaxi from "../../common/Czcprivate.js"; //出租车专线
import $lyfw from '@/common/LYFW/LyfwFmq.js' //旅游服务
import uQRCode from "@/common/uqrcode.js"
import $bcfw from '../../common/BCFW/bcfw.js'
export default {
	components: {
		uniSegmentedControl,
		uniPopup,
		//加载多方弹框组件
		uniPopup2,
		uniIcons,
		emptyData
	},
	data() {
		return {
			TaxiCost: 0, //价格
			countdown: 0,
			items: ['全部', '已完成', '进行中', '未支付', '已取消'],
			current: 0,
			index: 1,
			exitindex: 0, //订单判断值
			ticketOrderNumber: '', //门票订单编号
			orderIndex: 0, //订单调用数值
			orderIndexData: '', //二维码订单数据
			QRCodeArray: [{
				checkPlace: 'A5',
				seatNum: 'E1',
				lunchTime: '18:00',
				carNum: '闽C12345'
			}],
			info: [], //请求服务器订单列表
			userInfo: '', //个人信息
			finishArr: [],
			goingArr: [],
			unfinishArr: [],
			cancelArr: [],
			keYunTicketArray: [], //客运订单
			keYunTicket: [], //客运订单
			keYunPaymentData: '', //客运支付
			driverName: '张师傅', //司机姓名
			totalPrice: 32.5,
			orderType1: '',
			ctkyOrderNum: '', //传统客运订单号（退票需要）
			payType: [{
					typeName: '微信',
					typeColor: '#00C805',
					iconPath: '../../static/CZC/Wechatpay.png',
					value: 'wxpay',
					checked: true,
				},
				{
					typeName: '支付宝',
					typeColor: '#0EBDFF',
					iconPath: '../../static/CZC/Alipay.png',
					value: 'alipay',
					checked: false
				}
			],
			specialLineInfo: ''
		}
	},
	onLoad: function() {
		var that = this;
		//读取用户ID
		uni.getStorage({
			key: 'userInfo',
			success: function(data) {
				that.userInfo = data.data;
			}
		})
	},
	onShow: function() {
		//请求景区门票数据
		this.toFinished();
	},
	onPullDownRefresh: function() {
		this.toFinished();
	},
	methods: {
		changeTime: function(value) { //时间格式转换
			var date = new Date(value + "+08:00");
			var year = date.getFullYear();
			var mounth = date.getMonth() + 1;
			if (mounth < 10) {
				mounth = "0" + mounth;
			}
			var day = date.getDate();
			if (day < 10) {
				day = "0" + day;
			}
			var hours = date.getHours();
			if (hours < 10) {
				hours = "0" + hours;
			}
			var minutes = date.getMinutes();
			if (minutes < 10) {
				minutes = "0" + minutes;
			}
			return year + '-' + mounth + '-' + day + " " + hours + ':' + minutes;
		},
		//-------------------------支付页面-------------------------
		payDetail: function() {
			//支付详情
			uni.navigateTo({
				url: '../CZC/PriceDetail'
			});
		},
		closePayment: function() {
			//关闭
			let that = this;
			that.closeBottomPopup();
		},
		payment: function(e) {
			//支付
			let that = this;
			var timeStamp = new Date().getTime();
			uni.requestPayment({
				provider: e.detail.value.chooseType,
				orderInfo: '111',
				timeStamp: timeStamp,
				nonceStr: '',
				package: '',
				paySign: '',
				success: function(res) {
					// console.log(res);
					uni.navigateTo({
						url: '../CZC/PaymentSuccess'
					});
				},
				fail: function(res) {
					// console.log(res);
					uni.navigateTo({
						url: '../CZC/PaymentFail'
					});
				}
			});
		},
		openBottomPopup: function() {
			this.$nextTick(function() {
				this.$refs['bottomPopup'].open();
			});
		},
		closeBottomPopup: function() {
			this.$nextTick(function() {
				this.$refs['bottomPopup'].close();
			});
		},

		back: function() {
			var that = this;
			uni.switchTab({
				url: '/pages/Home/Index',
			});
		},
		//------------------------------------------------客运开始------------------------------------------------
		//-------------------------客运用户详情-------------------------
		getUserInfo() {
			var that = this;
			//读取用户ID
			uni.getStorage({
				key: 'userInfo',
				success: function(data) {
					that.userInfo = data.data;
					console.log('用户信息', that.userInfo);
					that.getKeYunOrderInfo();
					that.getArrayInfo();
				},
				fail(res) {
					// console.log('错误', res);
				}
			})
		},

		//-------------------------请求客运订单数据-------------------------
		getKeYunOrderInfo: function() {
			var that = this;
			uni.request({
				url: 'http://zntc.145u.net/api/ky/searchOrder',
				method: 'GET',
				header: {
					'content-type': 'application/json'
				},
				data: {
					clientID: that.userInfo.userId
				},
				success: (res) => {
					uni.stopPullDownRefresh();
					console.log('11111', res.data);
					that.ctkyOrderNum = res.data.orderNumber;
					if (res.data.status == true) {
						for (var i = 0; i < res.data.data.length; i++) {
							that.info.push(res.data.data[i]);
						}
						for (var i = 0; i < res.data.data.length; i++) {
							if (res.data.data[i].state == '5') {
								that.finishArr.push(res.data.data[i]);
							} else if (res.data.data[i].state == '4') {
								that.goingArr.push(res.data.data[i]);
							} else if (res.data.data[i].state == '7') {
								that.unfinishArr.push(res.data.data[i]);
							} else if (res.data.data[i].state == '6' || res.data.data[i].state == '9') {
								that.cancelArr.push(res.data.data[i]);
							}
						}
						//出租车请求数据
						that.loadczcData();
					} else if (res.data.status == false) {
						// console.log('无客运车票数据');
						//出租车请求数据
						that.loadczcData();
					}
				},
				fail(res) {
					//请求数据失败，停止刷新
					uni.stopPullDownRefresh();
					// console.log('错误', res);
				}
			})
		},
		//-------------------------判断订单状态-------------------------
		getCtkyOrderStatus(param) {
			if (param == 4) {
				return '进行中'
			} else if (param == 5) {
				return '已完成'
			} else if (param == 6) {
				return '已退票'
			} else if (param == 7) {
				return '未支付'
			} else if (param == 9) {
				return '已撤销'
			} else if (param == 22) {
				return '已改签'
			}
		},
		//-------------------------跳转到详情页-------------------------
		keYunDetail: function(res) {
			console.log(res)
			uni.navigateTo({
				url: '../CTKY/TraditionSpecial/Order/orderDetail?orderInfo=' + JSON.stringify(res)
			})
		},
		// -------------------------客运退票-------------------------
		keYunRefundTicket: function(orderNumber) {
			console.log(orderNumber)
			var that = this;
			uni.request({
				url: 'http://zntc.145u.net/api/ky/RefundTicket_Flow',
				method: 'GET',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					orderNumber: orderNumber,
				},
				success: (respones) => {
					console.log('删除结果', respones)
					if(respones.data.status == true){
						uni.hideLoading()
						uni.showToast({
							title:'退票成功'
						})
						this.$refs.popup2.close()
						uni.startPullDownRefresh();
					}else {
						uni.hideLoading()
						uni.showToast({
							title:'退票失败',
							icon:'none'
						})
						uni.startPullDownRefresh();
					}
				},
				fail: (respones) => {
					uni.hideLoading()
					console.log(respones)
					uni.showToast({
						title:'服务器异常，请联系客服'
					})
				}
			})
		},
		// -------------------------客运取消-------------------------
		keYunCancelTicket: function(orderNumber) {
			console.log(orderNumber)
			var that = this;
			uni.request({
				url: 'http://zntc.145u.net/api/ky/CancelTicket_Flow',
				method: 'GET',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					orderNumber: orderNumber,
				},
				success: (respones) => {
					// alert(respones.data)
					console.log('取消结果', respones)
					if(respones.data.status == true){
						uni.hideLoading()
						uni.showToast({
							title:'取消成功'
						})
						this.$refs.popup3.close()
						uni.startPullDownRefresh();
					}else {
						uni.hideLoading()
						uni.showToast({
							title:'取消失败',
							icon:'none'
						})
						this.$refs.popup3.close()
						uni.startPullDownRefresh();
					}
				},
				fail: (respones) => {
					// alert(respones.data.msg)
					uni.hideLoading()
					console.log(respones)
					uni.showToast({
						title:'服务器异常，请联系客服'
					})
					this.$refs.popup3.close()
				}
			})
		},
		// -------------------------客运支付-------------------------
		keYunPay: function(orderNumber) {
			// var orderInfo = this.info[index];
			console.log(orderNumber);
			this.getTicketPaymentInfo(orderNumber);
		},
		//--------------------------获取车票支付参数--------------------------
		getTicketPaymentInfo: function(orderNumber) {
			// console.log('支付参数', orderNumber);
			var that = this;
			var timer = null;
			that.timer = timer;
			uni.showLoading();
			timer = setInterval(function() {
				uni.request({
					url: 'http://zntc.145u.net/api/ky/SellTicket_Flow',
					method: 'GET',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					data: {
						orderNumber: orderNumber,
					},
					success: (res) => {
						console.log('支付参数返回数据', res);
						if (res.data.status == true) {
							uni.hideLoading();
							var info = JSON.parse(res.data.msg);
							if (info.oldState == '结束') {
								uni.showToast({
									title:'订单已支付',
									icon:'none'
								})
								clearInterval(timer);
							} else {
								clearInterval(timer);
								that.keYunPaymentData = JSON.parse(res.data.msg);
								// console.log('支付参数返回数据', that.keYunPaymentData);
								that.keYunPayment();
							}
						} else if (res.data.status == false) {
							uni.hideLoading();
							var info = JSON.parse(res.data.msg);
							if (info.oldState == '结束') {
								uni.showToast({
									title:'订单已超时',
									icon:'none'
								})
								clearInterval(timer);
							} else {
								uni.showModal({
									content: info.oldState,
									showCancel: false
								})
							}
						}
					},
					fail(res) {
						uni.hideLoading();
						//回调失败，取消定时器
						clearInterval(timer);
					}
				})
