				return '已完成'
			} else if (param == '5') {
				return '待支付'
			} else if (param == '1') {
			 	return '待发车'
			 }else if (param == '8'||param == '9') {
				return '已取消'
			} else if (param == '11') {
				return '待补款'
			}
		}
	}
}
